$( document ).ready(function() {
    $("#btn").click(
		function(){
			sendAjaxForm('result_form', 'ajax_form', 'action_ajax_form.php');
			return false; 
		}
	);
});
 
function sendAjaxForm(result_form, ajax_form, url) {
    $.ajax({
        url:     "../php/action_ajax_form.php", //url —Å—Ç—Ä–∞–Ω–∏—Ü—ã (action_ajax_form.php)
        type:     "POST", //–º–µ—Ç–æ–¥ –æ—Ç–ø—Ä–∞–≤–∫–∏
        dataType: "html", //—Ñ–æ—Ä–º–∞—Ç –¥–∞–Ω–Ω—ã—Ö
        data: $("#"+ajax_form).serialize(),  // –°–µ–∞—Ä–∏–ª–∏–∑—É–µ–º –æ–±—ä–µ–∫—Ç
        success: function(response) { //–î–∞–Ω–Ω—ã–µ –æ—Ç–ø—Ä–∞–≤–ª–µ–Ω—ã —É—Å–ø–µ—à–Ω–æ
        	result = $.parseJSON(response);
        	$('#result_form').html('–ò–º—è: '+result.name+'<br>–¢–µ–ª–µ—Ñ–æ–Ω: '+result.phonenumber);
    	},
    	error: function(response) { // –î–∞–Ω–Ω—ã–µ –Ω–µ –æ—Ç–ø—Ä–∞–≤–ª–µ–Ω—ã
            $('#result_form').html('–û—à–∏–±–∫–∞. –î–∞–Ω–Ω—ã–µ –Ω–µ –æ—Ç–ø—Ä–∞–≤–ª–µ–Ω—ã.');
    	}
 	});
}